import loadErr from '@/components/loadErr.vue'
import noListData from '@/components/noListData.vue'
import { mapGetters, mapActions } from 'vuex'

export default {
	data() {
		return {
			userList: [],
			isLoadErr: false,
			userId: ''
		}
	},
	components: { loadErr, noListData },
	computed: {
		addTop() {
			return this.CustomBar + 'px'
		}
	},
	onShow() {
		this.userList = []
		this.getUserList()
	},
	methods: {
		...mapActions([
			'toGetTvlObj'
		]),
		toSelectTraveler(idx) {
			if(this.userId == this.userList[idx].id) {
				this.userId = ''
			}else {
				this.userId = this.userList[idx].id
			}
		},
		toAddTraveler(type) {
			uni.navigateTo({
				url: `/pages/optTraveler/optTraveler?type=${type}`
			})
		},
		toEditTvl(type, idx) {
			this.toGetTvlObj(this.userList[idx])
			uni.navigateTo({
				url: `/pages/optTraveler/optTraveler?type=${type}`
			})
		},
		toDeleteTvl(idx) {
			const $this = this
			uni.showModal({
				title: '',
				content: `确认删除出行人 ${$this.userList[idx].ch_name}`,
				cancelColor: '#909399',
				confirmColor: '#FFDA4C',
				success: (res) => {
					if(res.confirm) {
						$this.deleteTvlMay(idx)
					}else {
						this.$api.warnNotice('已取消操作')
					}
				},
				fail: () => {
					this.$api.warnNotice('操作失败')
				}
			})
		},
		deleteTvlMay(idx) {
			const $this = this
			this.$api.ajax({
				url: this.$config.deleteUser,
				method: 'DELETE',
				data: {
					id: this.userList[idx].id,
					token: '123456'
				}
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					uni.showToast({
						title: '操作成功',
						icon: 'success',
						success: () => {
							$this.userList.splice(idx, 1)
						}
					})
				}else {
					this.$api.warnNotice(res.data.message)
				}
			})
			.catch((err) => {
				this.$api.warnNotice('操作失败')
			})
		},
		toRelaodData() {
			this.isLoadErr = false
			this.userList = []
			this.getUserList()
		},
		getUserList() {
			uni.showLoading({
				title: '数据加载中',
				mask: true
			})
			this.$api.ajax({
				url: this.$config.userList,
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					let arr = res.data.data
					
					this.userList = arr
					
					setTimeout(() => {
						uni.hideLoading();
					}, 500)
				}else {
					if(this.index == 1) {
						this.isLoadErr = true
					}else {
						this.$api.warnNotice(res.data.message)
					}
					uni.hideLoading();
				}
			})
			.catch((err) => {
				if(this.index == 1) {
					this.isLoadErr = true
				}else {
					this.$api.warnNotice('评论列表加载失败')
				}
				uni.hideLoading();
			})
		}
	}
}
